const AV = require("leancloud-storage");

export const saveResult = ({
    result,
    name,
}) => {
    const { Query, User } = AV;
    const PerfumeResult = AV.Object.extend("PerfumeResult");
    const instance = new PerfumeResult();

    instance.set("itemType", result)
    instance.set("name", name)
    instance.save().catch(err => {
        console.error('storage error', err)
    })
}